import React, { useEffect, useState } from 'react';
import './About.css';

const About = () => {
  const [isVisible, setIsVisible] = useState(false);
  const [logoSrc, setLogoSrc] = useState(null);

  useEffect(() => {
    const handleScroll = () => {
      const aboutSection = document.querySelector('.about-section');
      const sectionTop = aboutSection.getBoundingClientRect().top;
      const windowHeight = window.innerHeight;

      // Add class to fade in the About section
      setIsVisible(sectionTop < windowHeight * 0.8);

      // Lazy load the logo when the section is visible
      if (sectionTop < windowHeight * 0.8 && !logoSrc) {
        import('./image/logo.png').then((module) => {
          setLogoSrc(module.default);
        });
      }
    };

    window.addEventListener('scroll', handleScroll);

    // Initial check
    handleScroll();

    return () => {
      window.removeEventListener('scroll', handleScroll);
    };
  }, [logoSrc]);

  return (
    <div className={`about-section ${isVisible ? 'visible' : ''}`}>
      <div className="about-header">
        <h1>About</h1>
      </div>
      <div className="about-content">
        <p>
          <span className="name">Abdelkodous</span> <span className="pronouns">(HE/HIM)</span> 
           visionary creative redefining the digital landscape. With a strong foundation in software development,
           he fearlessly embraces freelancing and entrepreneurship. Based in Tunisia, he creates trends through the artistry of marketing, 
           video editing, and innovative 3D design. Witness the extraordinary journey where imagination meets reality.
        </p>
      </div>
      <div className="about-footer">
        {logoSrc && <img src={logoSrc} alt="Company Logo" className="company-logo" />} 
        <div className="company-info">
          <p className="position">Creative Developer and Visionary</p>
          <p className="company">CDV</p>
        </div>
      </div>
    </div>
  );
};

export default About;
